<template>
  <div id="cart"
  class="close_cart_event"
  :class="[{'member--loggedin': this.user.id !== ''}]"
  @click.prevent="close_cart($event)">
    <div class="cart__container close_cart_event"
    @click.prevent="close_cart($event)">
      <div class="cart__frame">
        <div class="cart__frame__arrow"></div>
        <div class="cart">
          <div class="cart__close">
            <a href="#" @click.prevent="cart.is_shown = false">
              <img src="../assets/icon/close_menu.png" alt="">
            </a>
          </div>
          <div class="cart__main">
            <p class="noProduct">目前購物車內尚無任何品項</p>
            <ul class="product">
              <li v-for="(item, index) in cart.products"
              :key="index">
                <div class="product__delete">
                  <a href="#" @click.prevent="edit_product(0, item.id)">
                    <img src="../assets/icon/remove_item.png" alt="">
                  </a>
                </div>
                <div class="product__img">
                  <a href="#">
                    <img :src="item.img" alt="">
                  </a>
                </div>
                <div class="product__main">
                  <a href="#">
                    <p class="product__title">{{item.title}}</p>
                    <p class="product__id">{{item.id}}</p>
                    <div class="product__price">NT${{item.price}}</div>
                  </a>
                  <div class="product__quantity">
                    <p>數量</p>
                    <b-form-spinbutton
                    v-model="item.quantity" min="0" max="100"
                    @change="edit_product($event, item.id)">
                    </b-form-spinbutton>
                  </div>
                </div>
              </li>
            </ul>
            <p class="cart__sum">NT ${{cart.sum}}</p>
          </div>
          <div class="cart__checkout">
            <a href="#"
            @click="go_page('front/checkout/checkout1')"
            >前往結帳</a>
          </div>
          <p class="cart__remark">台灣本島地區全站商品免運配送</p>
          <ul class="cart__functionBtns">
            <li>
              <a href="#">
                喜好項目
              </a>
            </li>
            <li>
              <a href="#">
                訂單
              </a>
            </li>
            <li>
              <a href="#">
                會員專區
              </a>
            </li>
            <li class="log_out">
              <a href="#">
                登出
              </a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['cart', 'user'],
  methods: {
    go_page(pageString) {
      this.$router.push(`/${pageString}`);
    },
    close_cart(e) {
      if ([...e.target.classList].includes('close_cart_event')) {
        this.cart.is_shown = false;
      }
    },
    edit_product(eventNum, id) {
      if (eventNum === 0) {
        this.cart.products.splice(this.cart.products
          .findIndex((item) => item.id === id), 1);
      }
      this.cart.sum = 0;
      this.cart.products.forEach((item) => {
        this.cart.sum += item.price * item.quantity;
      });
      this.$bus.$emit('rerender_cart');
    },
  },
};
</script>
