    <transition name="fade">
      <cart :cart='cart'
      :user='user'
      v-if="cart.is_shown"></cart>
    </transition>
    <div class="testblock" @click="go_page('front/login')"></div>
  </div>
</template>

<script>
import $ from 'jquery';
import Cart from '../components/Cart.vue';

export default {
  watch: {
    $route() {
      this.change_route();
      this.toggle_banner();
    },
  },
  props: ['products', 'cart', 'news', 'mailbox', 'user', 'orders', 'coupons', 'users'],
  components: { Cart },
  data() {
    return {
      routes: [],
      category: [],
    };
  },
  methods: {
    go_page(pageString) {
      this.$router.push(`/${pageString}`);
    },
    open_nav() {
      $('#front').addClass('header__nav--open');
      $('.header__nav__list').animate({
        left: 20,
        opacity: 1,
      });
      this.toggle_arrow('header__nav');
    },
    close_nav() {
      $('#front').removeClass('header__nav--open');
      $('.header__nav__list').css({
        left: 70,
        opacity: 0,
      });
    },
    toggle_arrow(elStr) {
      const parentWidth = $(`.${elStr}__list`).width();
      let childrenWidth = 0;
      document.querySelectorAll(`.${elStr}__list li`).forEach((li) => {
        childrenWidth += $(li).outerWidth(true);
      });
      childrenWidth = Math.floor(childrenWidth);
      const parentLeft = $(`.${elStr}__list`).scrollLeft();
      if (parentWidth < childrenWidth) {
        if (parentLeft === 0) {
          $(`.${elStr}__leftArrow a`).fadeOut();
        } else {
          $(`.${elStr}__leftArrow a`).fadeIn();
        }
        if (childrenWidth - parentLeft <= parentWidth) {
          $(`.${elStr}__rightArrow a`).fadeOut();
        } else {
          $(`.${elStr}__rightArrow a`).fadeIn();
        }
      } else {
        $(`.${elStr}__list`).scrollLeft(0);
        $(`.${elStr}__leftArrow a`).fadeOut();
        $(`.${elStr}__rightArrow a`).fadeOut();
      }
    },
    scroll_list(elStr, direction) {
      if (direction) {
        // true = scrollLeft++
        $(`.${elStr}__list`).animate({
          scrollLeft: '+=50',
        }, 'fast', () => {
          this.toggle_arrow(elStr);
        });
      } else {
        $(`.${elStr}__list`).animate({
          scrollLeft: '-=50',
        }, 'fast', () => {
          this.toggle_arrow(elStr);
        });
      }
    },
    bind_list_scroll(elStr) {
      $(window).bind('mousewheel', (e) => {
        if (e.originalEvent.deltaY > 0) {
          // 往下滑  scrollLeft++
          this.scroll_list(elStr, true);
        } else {
          this.scroll_list(elStr, false);
        }
      });
    },
    unbind_list_scroll() {
      $(window).unbind('mousewheel');
    },
    go_top() {
      $('html').animate({ scrollTop: '0' }, 500);
    },
    change_route() {
      const targetIndex = this.routes
        .findIndex((item) => this.$route.fullPath
          .includes(item));
      const itemArray = document.querySelectorAll('.header__nav__list li a');
      const activeIndex = [...itemArray]
        .map((item) => [...item.classList])
        .findIndex((item) => item.includes('active'));
      const chineseArray = [...document.querySelectorAll('.header__nav__list li a .chinese')]
        .map((item) => item.innerHTML);

      document.title = `${chineseArray[targetIndex]} | GOODAFTER 日青方好`;
      $(itemArray[activeIndex]).removeClass('active');
      $(itemArray[targetIndex]).addClass('active');
    },
    toggle_banner() {
      if (this.$route.fullPath.includes('online_store')
      || this.$route.fullPath.includes('news')) {
        $('#front').addClass('banner--hidden');
      } else {
        $('#front').removeClass('banner--hidden');
      }
    },
    rerender_cart() {
      this.cart.is_shown = true;
      this.$nextTick(() => {
        if (this.cart.products.length === 0) {
          $('#front').removeClass('cart--hasProduct');
          $('#cart').addClass('noProduct');
          $('#cart').removeClass('hasProduct');
        } else {
          $('#front').addClass('cart--hasProduct');
          $('#cart').removeClass('noProduct');
          $('#cart').addClass('hasProduct');
        }
      });
    },
  },
  created() {
    this.routes = [...document.querySelectorAll('.header__nav__list li a')]
      .map((item) => item.dataset.path);

    this.category = this.products
      .map((obj) => obj.category)
      .flat(1);

    this.category = [...new Set(this.category.map((item) => JSON.stringify(item)))]
      .sort()
      .map((item) => JSON.parse(item))
      .map((item) => {
        const newObj = {};
        newObj.path = `front/online_store/${item.english}`;
        newObj.chinese = item.chinese;
        newObj.english = item.english;
        return newObj;
      });

    this.cart.sum = 0;
    this.cart.products.forEach((item) => {
      this.cart.sum += item.price * item.quantity;
    });
  },
  mounted() {
    this.change_route();
    this.toggle_banner();
    this.toggle_arrow('header__nav');
    this.toggle_arrow('category');
    $(window).resize(() => {
      if ($(window).width() <= 1023) {
        this.toggle_arrow('header__nav');
        this.toggle_arrow('category');
      } else {
        this.open_nav();
      }
    });
    $(window).scroll(() => {
      if ($(window).scrollTop() > 0) {
        $('#front').addClass('category--shadowed');
        $('#front').addClass('header--shadowed');
      } else {
        $('#front').removeClass('category--shadowed');
        $('#front').removeClass('header--shadowed');
      }
      if ($(window).scrollTop() >= $('.banner').height()) {
        $('#front').removeClass('header--shadowed');
        $('#front').addClass('category--fixed');
      } else {
        $('#front').removeClass('category--fixed');
      }
    });
    if (this.cart.products.length === 0) {
      $('#cart').addClass('noProduct');
      $('#cart').removeClass('hasProduct');
      $('#front').removeClass('cart--hasProduct');
    } else {
      $('#cart').removeClass('noProduct');
      $('#cart').addClass('hasProduct');
      $('#front').addClass('cart--hasProduct');
    }
    this.$bus.$on('rerender_cart', () => {
      this.rerender_cart();
    });
    this.$bus.$on('login', (form) => {
      // login_form: {
      // email: '',
      // remember_account: false,
      // password: '',
      // },
      const thisUser = this.users
        .find((item) => item.email === form.email);
      if (thisUser.password === form.password) {
        console.log('登入成功');
      } else {
        alert('帳號或密碼錯誤');
      }
    });
  },
};
</script>
